@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;300;400;500;700&display=swap);
body{font:100% "Open Sans",Helvetica,Arial,sans-serif;margin:0;padding:0}h1,h2,h3,h4,h5,h6{font:100% "Open Sans",Helvetica,Arial,sans-serif}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}

@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}.app{margin:0;padding:0;width:100%;height:100vh}.app .scroll-prompt{position:absolute;color:#fff;font-weight:300;font-size:0.8em;right:0;bottom:10px;background-color:rgba(0,0,0,0.2);padding:10px;font-family:'Roboto Mono', monospace;border-radius:5px}.app .api-update-message{font-family:'Roboto Mono', monospace;font-weight:300}.app .intro{width:100%;height:100%}.app .intro #intro-canvas{margin:0;padding:0;width:100%;height:100vh;width:100%;height:100%}.app .intro .progress-bar{position:absolute;top:50%;width:100%;text-align:center;opacity:0.85;height:10px}.app .intro .progress-bar .progress-wrap{width:200px;margin:0 auto;height:10px}.app .intro .progress-bar .progress-wrap .progress{border-radius:10px;height:10px}.app .intro .progress-bar .progress-wrap .progress .bar{border-radius:10px;height:10px}.app .intro .progress-bar .progress-wrap .progress .bar .progress{border-radius:10px;height:10px}.app .content{margin:0;padding:0;width:100%;height:100vh;background-image:url(/public/images/bg.svg);background-color:#000;background-size:cover;background-repeat:no-repeat;background-position:right;position:absolute;top:0;left:0;-webkit-animation:fadein 1s;animation:fadein 1s}.app .content .stars{display:block;width:100%}.app .content .stars-middle{display:block;width:80%;left:-80%;top:0;position:absolute;-webkit-transform:scaleX(-1);transform:scaleX(-1)}.app .content .prop-1{width:100px;margin-left:30%;margin-top:20px}.app .content .prop-2{width:60px;margin-left:30%;margin-top:70px;-webkit-transform:rotate(20deg);transform:rotate(20deg)}.app .content .prop-3{width:70px;margin-left:50%;margin-top:45px;-webkit-transform:rotate(20deg);transform:rotate(20deg)}.app .content .prop-4{width:200px;margin-left:70%;margin-top:50px}.app .content .content-block{display:flex;align-items:center;justify-content:center}

.sol{text-align:center;color:#fff;background-color:rgba(0,0,0,0.2);padding:20px;border-radius:10px}.sol .summary .row .column:last-child{padding-left:10%;text-align:left}.sol .measurement-select{display:flex;position:absolute;right:15px}.sol .measurement-select span{display:inline-flex;padding:0 5px;font-size:20px}.sol .selected{font-weight:400}.sol .widget-footer .row .column div.checkbox-label{display:inline-block;margin:0 7px;min-height:1.25rem;vertical-align:super}.sol .widget-footer .row .column .ui.slider.checkbox input:checked ~ .box:after,.sol .widget-footer .row .column .ui.slider.checkbox input:checked ~ label:after{left:2.6rem}.sol .widget-footer .row .column .ui.slider.checkbox .box:after,.sol .widget-footer .row .column .ui.slider.checkbox label:after{width:1rem;height:1rem;top:0}.sol .widget-footer .row .column .ui.slider.checkbox input:checked ~ .box:before,.sol .widget-footer .row .column .ui.slider.checkbox input:checked ~ label:before,.sol .widget-footer .row .column .ui.slider.checkbox input:focus:checked ~ .box:before,.sol .widget-footer .row .column .ui.slider.checkbox input:focus:checked ~ label:before,.sol .widget-footer .row .column .ui.slider.checkbox .box:before,.sol .widget-footer .row .column .ui.slider.checkbox label:before{background-color:#76559c !important}.sol .small{font-size:12px;font-weight:300;letter-spacing:0.03em}.sol .row{font-family:'Roboto Mono', monospace}.sol .row .icon{text-align:center}.sol .row .icon svg{max-width:40px;margin-bottom:10px;fill:#fff}.sol .row p{margin-bottom:5px;font-weight:100}.sol .row p .label{font-weight:400}.sol .row p .clickable{cursor:pointer}.sol h1{font-weight:400;margin:0;font-size:3em}.sol h3{font-weight:300;margin:0;font-size:1.5em}

.overview{text-align:center;color:#fff;background-color:rgba(0,0,0,0.2);padding:20px;border-radius:10px;font-family:'Roboto Mono', monospace;font-weight:100;font-size:14px;margin-top:-20px}.overview div.checkbox-label{display:inline-block;margin:0 7px;min-height:1.25rem;vertical-align:super}.overview .ui.slider.checkbox input:checked ~ .box:after,.overview .ui.slider.checkbox input:checked ~ label:after{left:2.6rem}.overview .ui.slider.checkbox .box:after,.overview .ui.slider.checkbox label:after{width:1rem;height:1rem;top:0}.overview .ui.slider.checkbox input:checked ~ .box:before,.overview .ui.slider.checkbox input:checked ~ label:before,.overview .ui.slider.checkbox input:focus:checked ~ .box:before,.overview .ui.slider.checkbox input:focus:checked ~ label:before,.overview .ui.slider.checkbox .box:before,.overview .ui.slider.checkbox label:before{background-color:#76559c !important}.overview .ui.table thead th{font-weight:400;text-align:center}.overview .ui.table tr td{text-align:center}.overview .sol-item h1,.overview .sol-item h3{margin:0}.overview .sol-item .icon svg{max-width:18px;fill:#fff;display:inline-block}

.graphs{text-align:center;color:#fff;background-color:rgba(0,0,0,0.2);padding:20px;border-radius:10px;font-family:'Roboto Mono', monospace;font-weight:100;max-width:940px;width:100%}.graphs h1{font-size:1em;font-family:'Roboto Mono', monospace;font-weight:300}.graphs .graph-wrapper{min-height:320px;margin-bottom:2rem;max-width:95%;margin-left:2.5%}.graphs .graph-wrapper svg{overflow:visible;width:100%}.graphs .graph-wrapper svg .axis{color:#a289c2}

